/* Cores globais */
$yellow: #ffe666;
$primary-color: #c4e877;
$green-color: #d8f0a5;
$Second-color: #d8f0a5;
$blue-color: #1ddcff;
$text-color: #0000;
$mustard-yellow: #fbc90b;

@font-face {
  font-family: 'universBold';
  src: url("../Assets/univers-next-pro-cufonfonts/UniversNextProBold.ttf");

  font-family: 'universRegular';
  src: url("../Assets/univers-next-pro-cufonfonts/UniversNextProRegular.ttf");

  font-family: 'universMedium';
  src: url("../Assets/univers-next-pro-cufonfonts/UniversNextProMediumCondensed.ttf");

}

.navbar-brand {
  gap: 40px;
  display: flex;
  padding-left: 20px;

  span {
    font-size: 28px;
  }
}

.button-header {
  margin-right: 20px;
  color: #ffff;
  padding: 8px 15px;
  border-radius: 6px;
  background-color: #486018;
  border: 0;

}

.button-banner {
  margin-right: 20px;
  color: #ffff;
  padding: 8px 20px;
  border-radius: 6px;
  background-color: #486018;
  border: 0;
  margin-top: 50px;
}

.fixed-top {
  width: 90%;
  margin: auto;
  border-radius: 0px 0px 10px 10px;
}

/* Css menu */
.offcanvas.offcanvas-end {
  width: 100%;
  border-left: var(--bs-offcanvas-border-width) solid var(--bs-offcanvas-border-color);
  transform: translateY(-100%);
  background-color: $yellow;
  padding-bottom: 10px;
}

.header-text {
  display: flex;
  flex-direction: column;
  margin: auto;
  padding-bottom: 10px;
}

.offcanvas-header span {
  font-size: 25px;
}

.offcanvas-header img {
  width: 80px;
  margin: auto;
}

.offcanvas-body {
  margin: auto;
}

.nav-item a {
  text-align: center;
  font-size: 40px;
}

.text-banner {
  padding: 200px 100px;
  background-image: url("../Assets/banner.png");

  .background-h1 {
    width: 42%;
  }

  h1 {
    background-color: #c4e877;
    font-size: 60px;
    letter-spacing: -1px;
    font-weight: 600;
    font-family: universMedium;
    width: 63%;
  }

  h2 {
    background-color: #FFC860;
    width: 63%;
    font-family: universMedium;
    font-weight: 400;
    letter-spacing: -1.4px;
    font-size: 30px;
  }


}

/*Primeira section*/

.col p {
  text-align: initial;
}

.background-img-text {
  background-color: #c4e877;
  display: flex;

  h3 {
    text-align: initial;
    margin-left: 20px;
    align-self: center;
    font-weight: 500;
    font-family: universMedium;
  }
}

.section-primary {
  margin-top: 100px;
  display: flex;
  margin-bottom: 100px;

  p {
    font-weight: 400;
    font-size: 18px;
  }

  .coluna-1 {
    padding-right: 50px;
  }

  .title-section-1 {
    font-size: 40px;
    text-align: initial;
  }
}

.second-col {
  margin-top: 40px;
}

.background-img-text-2 {
  background-color: #c4e877;
  display: flex;
  flex-direction: column;

  h3 {
    text-align: initial;
    margin-left: 20px;
    align-self: center;
    font-weight: 400;
    font-family: universMedium;
    margin-top: -15px;
  }

  span {
    font-size: 70px;
    text-align: initial;
    margin-left: 20px;
    font-family: universMedium;
    font-weight: 600;
  }
}

.Section-second {
  display: flex;
  flex-direction: column;
  margin-bottom: 60px;

  h2 {
    text-align: initial;
    margin-bottom: 30px;
    font-weight: 600;
    font-size: 35px;
    font-family: universMedium;
  }
}

.section-third-content {
  margin: auto;
  text-align: initial;
  display: flex;
  margin-top: 100px;
  margin-bottom: 100px;

  p {
    text-align: initial;
    margin-bottom: 20px;
  }
}


.Text-background {
  background-color: #c4e877;
  font-size: 24px;
  font-family: universMedium;
  padding: 10px 10px;
  text-align: initial;
  margin-bottom: 20px;
  margin-top: -30px;
}

.Container-Cards {
  display: flex;
  gap: 50px;
  margin-top: 100px;
  justify-content: center;
  flex-wrap: wrap;

  .Card-1 {
    background-color: $green-color;
    width: 330px;
    padding-bottom: 0;
    display: flex;
    flex-direction: column;
    height: 100px;

    span {
      font-size: 65px;
      text-align: initial;
      font-weight: 500;
      font-family: universMedium;
      padding-left: 10px;
    }

    p {
      font-size: 30px;
      text-align: initial;
      padding-left: 20px;
      background-color: $primary-color;
      padding-bottom: 120px;
      font-family: universMedium;
      font-family: 300;
    }
  }

  .Card-2 {
    background-color: $green-color;
    width: 330px;
    padding-bottom: 0;
    display: flex;
    flex-direction: column;
    height: 100px;

    span {
      font-size: 65px;
      text-align: initial;
      font-weight: 500;
      font-family: universMedium;
      padding-left: 10px;
    }

    p {
      font-size: 30px;
      text-align: initial;
      padding-left: 20px;
      background-color: $primary-color;
      padding-bottom: 77px;
      font-family: universMedium;
      font-family: 300;
    }
  }

  .Card {
    background-color: $green-color;
    width: 330px;
    padding-bottom: 0;
    display: flex;
    flex-direction: column;
    height: 200px;

    span {
      font-size: 65px;
      text-align: initial;
      font-weight: 500;
      font-family: universMedium;
      padding-left: 10px;
    }

    p {
      font-size: 32px;
      text-align: initial;
      padding-left: 20px;
      background-color: $primary-color;
      padding-bottom: 10px;
      font-family: universMedium;
      font-family: 300;
      line-height: 35px;

      .span-prag {
        font-size: 14px;
        margin-top: 11px;
        width: 65%;
        display: flex;
        flex-direction: column;
        line-height: 20px;
        color: #8B8B8D;
      }
    }
  }
}

.Banner-third {
  background-image: url(../Assets/img.png);

  h2 {
    justify-content: center;
    font-size: 80px;
    color: white;
    width: 60%;
    display: flex;
    padding-top: 100px;
    padding-bottom: 100px;
  }
}
.map-text  {
  display: flex;
  flex-direction: column;
  margin-top: 50px;
  p {
    text-align: initial;
    font-size: 20px;
  }
}
.Container-Box {
  display: flex;
  gap: 20px;
  margin-bottom: 100px;
  justify-content: center;

  flex-wrap: wrap;
  margin-top: 50px;

  h2 {
    font-size: 30px;
    font-weight: 600;
    
  }

  .Box {
    background-color: #d8f0a5;
    padding: 10px 30px;

    span {
      font-size: 50px;
    }

    p {
      font-size: 25px;
    }

    li {
      margin-bottom: 20px;
      font-size: 20px;
      text-align: initial;
    }
  }

  .Box-icon {
    img {
      background-color: #c4e877;
      padding: 30px;
    }
  }
}

.Box-img {
  background-color: #1ddcff;
  width: 400px;
  align-self: center;
  height: 200px;
  margin-left: -100px;
}

.Container-people {
  display: flex;
  margin-top: 100px;
  justify-content: center;

}

.Container-act {
  display: flex;
  margin: auto;
  flex-direction: column;
  width: 80%;

  .Box-Icon-text {
    display: flex;

    p {
      background-color: #FFE666;
      padding: 36px 40px;
      font-size: 30px;
      margin-left: 20px;
    }
  }


}

.Icon-box {

  img {
    background-color: #FFF1A8;
    padding: 30px;
  }

}

/*footer*/

.img-footer {
  background-image: url(../Assets/img-footer.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  display: flex;
  height: 450px;
}

.box-orange {
  background-color: #F76C00;
  padding: 30px 50px;
  width: 750px;
  margin: auto;
  border-radius: 10px;
  font-size: 25px;
  color: #ffff;

  a {
    color: #ffff;
    text-decoration: none;
  }
}

.container-footer {
  display: flex;
  flex-direction: initial;
  justify-content: space-between;
  padding: 10px 60px;
  width: 90%;
  margin: auto;
}

.footer {
  display: flex;
  flex-direction: column;
}